/* Sidebar layout adjustments */
.sidebar-content {
  margin-left: 16rem; /* 256px - width of sidebar */
  width: calc(100% - 16rem);
}

@media (max-width: 768px) {
  .sidebar-content {
    margin-left: 0;
    width: 100%;
  }
}

.fade-in {
  animation: fadeIn 0.8s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.1);
}

/* Apple-inspired image hover effects */
.hover-image-container {
  overflow: hidden;
  position: relative;
}

.hover-image {
  transition: transform 0.7s cubic-bezier(0.19, 1, 0.22, 1), filter 0.7s cubic-bezier(0.19, 1, 0.22, 1);
}

.hover-image-container:hover .hover-image {
  transform: scale(1.05);
  filter: brightness(1.05);
}

/* Blog post card hover effect */
.blog-card {
  transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
  transform-origin: center bottom;
}

.blog-card:hover {
  transform: translateY(-8px) scale(1.01);
}

/* Interest item hover effect */
.interest-item {
  transition: all 0.4s cubic-bezier(0.19, 1, 0.22, 1);
}

.interest-item:hover {
  transform: translateY(-5px);
}

.interest-item:hover .interest-overlay {
  opacity: 1;
}

.interest-overlay {
  opacity: 0;
  transition: opacity 0.4s ease;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
}

/* Apple-inspired button hover effect */
.apple-button {
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  position: relative;
  overflow: hidden;
}

.apple-button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.apple-button:hover::after {
  opacity: 1;
}

.apple-button:active {
  transform: scale(0.98);
}

/* Glow effect for buttons and icons */
.glow-effect {
  position: relative;
  z-index: 1;
}

.glow-effect:hover {
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
}

.glow-effect:hover::before {
  opacity: 1;
}

.glow-effect::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.2)
  );
  border-radius: inherit;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

/* Icon hover animations */
.icon-hover {
  transition: all 0.3s ease;
}

.icon-hover:hover {
  transform: scale(1.2);
  filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.3));
}

/* Icon link animations */
.icon-link:hover svg {
  transform: scale(1.2);
  filter: drop-shadow(0 0 3px rgba(255, 255, 255, 0.2));
  transition: all 0.3s ease;
}

/* Info card hover effect */
.info-card {
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
  position: relative;
  overflow: hidden;
}

.info-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1);
}

.info-card:hover .info-card-content {
  transform: translateY(0);
  opacity: 1;
}

.info-card-content {
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

/* Terminal-style counter animation */
.terminal-counter-container {
  position: relative;
  font-family: monospace;
  display: inline-flex;
  align-items: center;
}

.terminal-counter {
  position: relative;
  display: inline-flex;
  align-items: center;
}

.terminal-cursor {
  display: inline-block;
  margin-left: 2px;
  animation: blink 1s step-end infinite;
}

@keyframes blink {
  from,
  to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Project card animations */
.project-card {
  position: relative;
  overflow: hidden;
}

.project-card::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.1) 50%, transparent 100%);
  transition: left 0.7s ease;
}

.project-card:hover::after {
  left: 100%;
}

/* Modal animations */
.modal-overlay {
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  animation: scaleIn 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Video player styles */
.video-player {
  position: relative;
  overflow: hidden;
  border-radius: 0.5rem;
}

.video-player video {
  width: 100%;
  height: auto;
  display: block;
}

.video-controls {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7), transparent);
  padding: 1rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.video-player:hover .video-controls {
  opacity: 1;
}

/* Book card styles */
.book-card {
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

.book-card:hover {
  transform: translateY(-8px) rotateY(5deg);
}

.book-card:hover .book-links {
  opacity: 1;
  transform: translateY(0);
}

.book-links {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

/* Contact form animations */
.contact-form-container {
  perspective: 1000px;
}

.contact-form {
  transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
  transform-style: preserve-3d;
}

.contact-form.sending {
  transform: rotateY(10deg);
  opacity: 0.7;
}

.contact-form.sent {
  transform: rotateY(180deg);
  opacity: 0;
}

.success-message {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  backface-visibility: hidden;
  transform: rotateY(180deg);
  transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
  opacity: 0;
}

.success-message.visible {
  opacity: 1;
  transform: rotateY(0);
}

/* Form input animations */
.form-input-container {
  position: relative;
  overflow: hidden;
}

.form-input-container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: hsl(var(--primary));
  transition: width 0.3s ease;
}

.form-input-container:focus-within::after {
  width: 100%;
}

.form-input-label {
  position: absolute;
  left: 0.75rem;
  top: 0.75rem;
  color: hsl(var(--muted-foreground));
  transition: all 0.3s ease;
  pointer-events: none;
}

.form-input:focus + .form-input-label,
.form-input:not(:placeholder-shown) + .form-input-label {
  transform: translateY(-1.25rem) scale(0.85);
  color: hsl(var(--primary));
}

/* Send button animation */
.send-button {
  position: relative;
  overflow: hidden;
}

.send-button::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.6s ease, height 0.6s ease;
}

.send-button:hover::before {
  width: 300%;
  height: 300%;
}

.send-button .send-icon {
  transition: transform 0.3s ease;
}

.send-button:hover .send-icon {
  transform: translateX(4px);
}

.send-button.sending .send-icon {
  animation: sendingPulse 1.5s infinite;
}

@keyframes sendingPulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

/* Timeline styles */
.timeline-container {
  position: relative;
}

.timeline-dot {
  box-shadow: 0 0 0 4px rgba(var(--primary), 0.1);
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-dot {
  transform: scale(1.2);
}

.timeline-line {
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-line {
  background-color: hsl(var(--primary));
}

/* Fancy list item animations */
.fancy-list-item {
  transition: all 0.3s ease;
}

.fancy-list-item:hover {
  transform: translateX(5px);
}

/* Fancy skill bar */
.fancy-skill-bar {
  transition: all 0.3s ease;
}

.fancy-skill-bar:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1);
}

.skill-icon-container {
  transition: all 0.3s ease;
}

.fancy-skill-bar:hover .skill-icon-container {
  transform: rotate(360deg);
}

.skill-dots .skill-dot {
  transition: all 0.3s ease;
}

.fancy-skill-bar:hover .skill-dot {
  background-color: hsl(var(--primary));
}

/* Quote animation */
.quote-container {
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.quote-container.fade-in {
  opacity: 1;
  transform: translateY(0);
}

.quote-container.fade-out {
  opacity: 0;
  transform: translateY(10px);
}

/* Kenya flag animation */
.kenya-flag-colors {
  background-size: 200% 200%;
}

@keyframes gradient-x {
  0% {
